.App {
  text-align: center;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;  
  background-color:#030011;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  overflow: hidden;
  background-position: center bottom -200px;
  background-color: #101318;
}

.App-content{
  min-height: 75vh;
  background-color: #030011;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow-y: hidden;
}

.App-link {
  color: #61dafb;
}

.title{
  font-family: 'Hind Siliguri', sans-serif;
  margin-bottom: 0;
  color: white;
  font-weight: lighter;
}


ul{
  font-size: 25px;
  font-family: 'Hind Siliguri', sans-serif;
  font-weight: lighter;
  list-style-type: none;
  overflow: hidden;
  justify-content: center;
  padding: 0;
}

li {
 justify-content: center;
 float: left;
 margin: 0;
}

.unSelected-title {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
  transition-duration: 0.3s;
  font-size:calc(10px + 2vmin);
  padding: 0px 4px;
  
}

.unSelected-title:hover{
  color: rgb(9, 132, 214);
  cursor: pointer;
}

.unSelected-title.selected {
  color: #0984d6;
  transform: scale(0.9);
}

.headtit{
  padding: 0px 10px;
}

.subtitle {
  font-size: 25px;
  color: rgb(9, 132, 214);
  margin: 0px;
}

.subsubtitle{
  font-size: 20px;
  color: rgb(9, 132, 214);
  margin: 0px;
}

.info{
  color:rgb(214, 214, 214);
  font-size: 16px;
  margin: 0px;
}

.hidden{
  width:0;
  height: 0;
  opacity: 0;
  transition: width, opacity ease-out 1s;
}

.hidden.show{
  width:100%;
  height: 100%;
  opacity: 1;
}


@keyframes in{
  fron{ font-size: 25px; color: aliceblue;}
  to{ font-size: 40px; color: darkgoldenrod;} 
}
@keyframes out{
  fron{ font-size: 40px;color: darkgoldenrod;}
  to{ font-size: 25px;color: aliceblue;}
}

@import url('https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@500&display=swap');


.fade-appear{
  opacity: 0; 
  z-index: 1;
  background-color: #030011;
}
.fade-appear.fade-appear-active{
  opacity: 1;
  transition: opacity 1000ms linear;

}


/* enter */
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 5000ms linear 5000ms;
}

/* exit */
.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 5000ms linear;
}
.fade-exit-done {
  opacity: 0;

}

@media screen and (min-width: 320px) {
  .title {
    font-size: calc(35px + 6 * ((100vw - 320px) / 680));
  }

  .App-Header{
    height: auto;
  }
}
@media screen and (min-width: 1000px) {
  .title {
    font-size: 55px;
  }
  .App-Header{
    min-height: 25vh;
  }
}

@media screen and (max-width:400){
  .title{
    width: 100px;
  }
  .footerText{
    font-size: calc(12px + 6 * ((100vw - 320px) / 680));
  }
}

@media screen and (min-width: 320px) {
  .info {
    font-size: calc(16px + 6 * ((100vw - 320px) / 680));
  }

}
@media screen and (min-width: 1000px) {
  .info {
    font-size: 16px;
  }
}

.footer{
  bottom: 10; 
  left: 0; 
  position: fixed;
  width: 100vw; 
  z-index: 1;
  background-color: #ca1515;
  color: #BBBBBB;
  padding: 0;
}

.footerText {
  color:rgb(214, 214, 214);
  font-size: 16px;
  margin: 0px;
}